/**
 * Form Input Password
 */

.tds-form-input-password {
  appearance: none;
  background-color: transparent;
  border: none;
  color: inherit;
  font-weight: var(--tds-font-weight--medium);
  inline-size: 100%;
  margin: 0;
  padding: 0;
  text-overflow: ellipsis;
  border-radius: var(--tds-border-radius--outline-only);
}

.tds-form-input-password:focus {
  outline: none;
}
