/**
 * tab-panel base styles
 * - Import this file only if you are running your own css post-processor and know what you're doing.
 */
.tds-tab-panel-list {
  inline-size: 100%;
}

.tds-tab-panel {
  display: none;
  visibility: hidden;
  transition: visibility 0.5s 0.5s;
}

.tds-tab-panel--active {
  display: block;
  visibility: inherit;
  transition: visibility 0.5s 0s;
}
