/* stylelint-disable property-no-vendor-prefix, no-duplicate-selectors, no-descending-specificity, font-family-no-duplicate-names */

:root,
.tds-component-preview {
  --tds-base-font_family: var(--tds-font-family--combined);
  --tds-base-font_size: var(--tds-font-size--30);
  --tds-base-line_height: var(--tds-line-height--30);
}

/* Defining "system" font stack used as fallback in --tds-font-family--combined variables */
@font-face {
  font-family: system;
  font-style: normal;
  font-weight: 300;
  src:
    local('.SFNSText-Light'),
    local('.HelveticaNeueDeskInterface-Light'),
    local('.LucidaGrandeUI'),
    local('Ubuntu Light'),
    local('Segoe UI Light'),
    local('Roboto-Light'),
    local('DroidSans'),
    local('Tahoma');
}

* {
  /* Set Box sizing at the element level */
  box-sizing: border-box;
  scroll-behavior: smooth;
}

html,
body {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  font-family: var(--tds-font-family--combined, --tds-base-font_family, system);
  margin: 0;
  padding: 0;
}

ol,
ul,
dl,
dt,
dd {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

figure,
figcaption {
  margin: 0;
  padding: 0;
}

/* Document
   ========================================================================== */

/**
 * Prevent adjustments of font size after orientation changes in iOS.
 */

html {
  -webkit-text-size-adjust: 100%;
}

/* Sections
   ========================================================================== */

/**
 * Remove the margin in all browsers.
 */

html {
  block-size: -webkit-fill-available;
}

body {
  margin: 0;
  min-block-size: 100vh;

  /* mobile viewport bug fix */
  min-block-size: -webkit-fill-available;
}

/* Text
   ========================================================================== */

p,
li {
  max-inline-size: 75ch;
}

li {
  margin: 0;
}

p {
  font-size: var(--tds-font-size--30);
  line-height: var(--tds-line-height--30);
}

/* Baseline typographic pairing, note that we want to avoid padding for ul + ul cases,
 * as those are more frequently used in components like list & navigation,
 * and very rarely in body copy */
p + p,
:is(ul, ol) + p,
p + :is(ul, ol) {
  padding-block-start: 14px;
}

small {
  font-size: var(--tds-font-size--20);
  line-height: var(--tds-line-height--10);
}

html,
body {
  font-family: var(--tds-font-family--combined, --tds-base-font_family, system);
  font-size: var(--tds-base-font_size);
  font-weight: 400;
  line-height: var(--tds-base-line_height);
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
small {
  display: block;
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
li,
small {
  line-height: var(--tds-line-height--30);
}

/* Grouping content
   ========================================================================== */

/**
 * 1. Add the correct box sizing in Firefox.
 * 2. Show the overflow in Edge and IE.
 */

hr {
  background: var(--tds-color--grey50, #d0d1d2);
  block-size: 1px;
  border: none;
  box-sizing: border-box; /* 1 */
  overflow: visible; /* 2 */
}

/**
 * 1. Correct the inheritance and scaling of font size in all browsers.
 * 2. Correct the odd `em` font sizing in all browsers.
 */

pre {
  font-family: monospace, monospace; /* 1 */
  font-size: 1em; /* 2 */
}

/* Text-level semantics
   ========================================================================== */

/**
 * 1. Remove the bottom border in Chrome 57-
 * 2. Add the correct text decoration in Chrome, Edge, IE, Opera, and Safari.
 */

abbr[title] {
  border-block-end: none; /* 1 */
  text-decoration: underline; /* 2 */
  text-decoration: underline dotted; /* 2 */
}

/**
 * Add the correct font weight in Chrome, Edge, and Safari.
 */

b,
strong {
  font-weight: 500;
}

/**
 * 1. Correct the inheritance and scaling of font size in all browsers.
 * 2. Correct the odd `em` font sizing in all browsers.
 */

code,
kbd,
samp {
  font-family: monospace, monospace; /* 1 */
  font-size: 1em; /* 2 */
}

/**
 * Add the correct font size in all browsers.
 */

small {
  font-size: 80%;
}

/**
 * Prevent `sub` and `sup` elements from affecting the line height in
 * all browsers.
 */

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

/* Forms
   ========================================================================== */

/**
 * 1. Change the font styles in all browsers.
 * 2. Remove the margin in Firefox and Safari.
 */

button,
input,
optgroup,
select,
textarea {
  font-family: inherit; /* 1 */
  font-size: 100%; /* 1 */
  font-weight: inherit;
  line-height: inherit; /* 1 */
  margin: 0; /* 2 */
}

/**
 * Show the overflow in IE.
 * 1. Show the overflow in Edge.
 */

button,
input {
  /* 1 */
  overflow: visible;
}

/**
 * Remove the inheritance of text transform in Edge, Firefox, and IE.
 * 1. Remove the inheritance of text transform in Firefox.
 */

button,
select {
  /* 1 */
  text-transform: none;
}

/**
 * Correct the inability to style clickable types in iOS and Safari.
 */

button,
[type='button'],
[type='reset'],
[type='submit'] {
  -webkit-appearance: button;
}

/**
 * Remove the inner border and padding in Firefox.
 */

button::-moz-focus-inner,
[type='button']::-moz-focus-inner,
[type='reset']::-moz-focus-inner,
[type='submit']::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

/**
 * Restore the focus styles unset by the previous rule.
 */

button:-moz-focusring,
[type='button']:-moz-focusring,
[type='reset']:-moz-focusring,
[type='submit']:-moz-focusring {
  outline: 1px dotted ButtonText;
}

/**
 * Correct the padding in Firefox.
 */

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

/**
 * 1. Correct the text wrapping in Edge and IE.
 * 2. Correct the color inheritance from `fieldset` elements in IE.
 * 3. Remove the padding so developers are not caught out when they zero out
 *    `fieldset` elements in all browsers.
 */

legend {
  box-sizing: border-box; /* 1 */
  color: inherit; /* 2 */
  display: table; /* 1 */
  max-inline-size: 100%; /* 1 */
  padding: 0; /* 3 */
  white-space: normal; /* 1 */
}

/**
 * Add the correct vertical alignment in Chrome, Firefox, and Opera.
 */

progress {
  vertical-align: baseline;
}

/**
 * Remove the default vertical scrollbar in IE 10+.
 */

textarea {
  overflow: auto;
}

/**
 * Correct the cursor style of increment and decrement buttons in Chrome.
 */

[type='number']::-webkit-inner-spin-button,
[type='number']::-webkit-outer-spin-button {
  block-size: auto;
}

/**
 * 1. Correct the odd appearance in Chrome and Safari.
 * 2. Correct the outline style in Safari.
 */

[type='search'] {
  -webkit-appearance: textfield; /* 1 */
  outline-offset: -2px; /* 2 */
}

/**
 * Remove the inner padding in Chrome and Safari on macOS.
 */

[type='search']::-webkit-search-decoration {
  -webkit-appearance: none;
}

/**
 * 1. Correct the inability to style clickable types in iOS and Safari.
 * 2. Change font properties to `inherit` in Safari.
 */

::-webkit-file-upload-button {
  -webkit-appearance: button; /* 1 */
  font: inherit; /* 2 */
}

/* Interactive
   ========================================================================== */

/* Remove animations and movemenets globally for users that have turned them off via browser setting */
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    transition: none !important;
    animation: none !important;
  }
}

:focus {
  outline: none;
}

/* Note: transition for this property has to be set on other elements that have transitions */
* {
  outline-offset: 2px;
}

/* Baseline focus indication */
:focus-visible {
  outline: var(--tds-outline--focus);
}

/* JS-based fallback for browsers that don't support :focus-visible (Safari >=15.0) */
:where(.tds--indicate-focus) :focus {
  outline: var(--tds-outline--focus);
}

/*
 * Add the correct display in Edge, IE 10+, and Firefox.
 */

details {
  display: block;
}

/*
 * Add the correct display in all browsers.
 */

summary {
  display: list-item;
}

/* Disabled State
   ========================================================================== */

/* Reduce visual prominence */
[disabled],
[disabled] ~ [class*='tds-form-label'],
.tds--disabled {
  cursor: not-allowed;
  opacity: var(--tds-ui-opacity-50);
}

.tds--disabled [disabled] {
  opacity: unset;
}

/* Prevent interactions */
[disabled] *,
[disabled]::before,
[disabled]::after,
[disabled] ~ [class*='tds-form-label'],
[disabled] ~ [class*='tds-form-label']::before,
[disabled] ~ [class*='tds-form-label']::after,
.tds--disabled * {
  pointer-events: none;
}
