/******************************************************************************
 Global Column Utilites
******************************************************************************/

.tds-columns {
  column-gap: 20px;
  column-width: auto;
  display: block;
}

.tds-columns--two {
  column-count: 2;
}

.tds-columns--three {
  column-count: 3;
}

/* Desktop modifiers for screens larger than 840px */
@media (--tds-tablet-portrait-up) {
  .tds-columns--two_on_desktop {
    column-count: 2;
  }

  .tds-columns--three_on_desktop {
    column-count: 3;
  }
}
