/******************************************************************************
 Global Alignment Utilities
******************************************************************************/

/* --- Container Alignment
---------------------------------------------------------------- */
.tds--align_center {
  display: block !important;
  margin: 0 auto !important;
}

.tds--align_start {
  display: block !important;
  margin-inline-end: auto !important;
  margin-inline-start: 0 !important;
}

.tds--align_end {
  display: block !important;
  margin-inline-end: 0 !important;
  margin-inline-start: auto !important;
}
