/* -- Button Theme Adjustments
----------------------------------------------------------------------------- */
.tds-theme--v6 {
  --tds-btn--font-size: var(--tds-font-size--20);
}

.tds-theme--v6 .tds-btn--large {
  --tds-border-radius--pill: 20px;
}

.tds-theme--v6 .tds-btn {
  text-transform: uppercase;
}
