/* Latin:
 * For localized stacks, the font-weight property should match the Gotham declaraction,
 * while the actual glyph set should be adjusted for the specific font.
 */

/* Bold */
@font-face {
  font-family: 'Gotham SSm';
  font-weight: 700;
  font-display: swap;
  font-style: normal;
  src:
    url('https://cdn-design.tesla.com/assets/GothamSSm-Bold_Web.woff2')
    format('woff2');
}

/* Medium */
@font-face {
  font-family: 'Gotham SSm';
  font-weight: 500;
  font-display: swap;
  font-style: normal;
  src:
    url('https://cdn-design.tesla.com/assets/GothamSSm-Medium_Web.woff2')
    format('woff2');
}

/* Book */
@font-face {
  font-family: 'Gotham SSm';
  font-weight: 400;
  font-display: swap;
  font-style: normal;
  src:
    url('https://cdn-design.tesla.com/assets/GothamSSm-Book_Web.woff2')
    format('woff2');
}

/* Light */
@font-face {
  font-family: 'Gotham SSm';
  font-weight: 300;
  font-display: swap;
  font-style: normal;
  src:
    url('https://cdn-design.tesla.com/assets/GothamSSm-Light_Web.woff2')
    format('woff2');
}

/* Extra Light */
@font-face {
  font-family: 'Gotham SSm';
  font-weight: 200;
  font-display: swap;
  font-style: normal;
  src:
    url('https://cdn-design.tesla.com/assets/GothamSSm-XLight_Web.woff2')
    format('woff2');
}

/* Chinese (Simplified, Traditional, Hong Kong):
 * Requested by China business teams:
 * Google Fonts don't load in CN, PingFang comes installed on majority of local systems
 */

@font-face {
  font-family: 'PingFang SC';
  font-weight: 500;
  src: local('PingFang SC Semibold');
}

@font-face {
  font-family: 'PingFang SC';
  font-weight: 400;
  src: local('PingFang SC Regular');
}

@font-face {
  font-family: 'PingFang TC';
  font-weight: 500;
  src: local('PingFang TC Semibold');
}

@font-face {
  font-family: 'PingFang TC';
  font-weight: 400;
  src: local('PingFang TC Regular');
}

@font-face {
  font-family: 'PingFang HK';
  font-weight: 400;
  src: local('PingFang HK Regular');
}

@font-face {
  font-family: 'PingFang HK';
  font-weight: 500;
  src: local('PingFang HK Semibold');
}

